//@import "./reset.scss"; 초기값들에 대해서 따로 만들고 import해서 사용 가능
$메인칼라: #ff0000;

.red {
  color: $메인칼라;
}
.my-alert {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert2 {
  @extend .my-alert;
  background: yellow;
}

@mixin 함수() {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.my-alert {
  @include 함수();
}
.my-alert p {
  margin-bottom: 0;
}

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}
